import React, { useContext, useState } from 'react'
import Layout from '../../../app/AppLayout'
import { AppContext } from '../../../context/app-context'

import { Button } from '@material-ui/core'
import GetAppIcon from '@material-ui/icons/GetApp'
import FormatIndentIncreaseIcon from '@material-ui/icons/FormatIndentIncrease'
import CircularProgress from '@material-ui/core/CircularProgress'

import {
  useTheme,
  createStyles,
  makeStyles,
  Theme
} from '@material-ui/core/styles'
import { palette } from '@material-ui/system'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      alignItems: 'center'
    },
    wrapper: {
      margin: theme.spacing(1),
      position: 'relative'
    },
    content: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1)
    },
    dlButton: {
      color: theme.palette.grey[200]
    },
    buttonProgress: {
      position: 'absolute',
      top: '50%',
      left: '50%',
      marginTop: -12,
      marginLeft: -12
    }
  })
)

interface FormState {
  loading: boolean
  generated: boolean
}

export interface AuthProps {}

const Auth: React.SFC<AuthProps> = () => {
  const classes = useStyles(useTheme())
  const [state, setState] = useState<FormState>({
    loading: false,
    generated: false
  })

  const appContext = useContext(AppContext)

  const submitHandler = (e: any) => {
    e.preventDefault()
    console.log('submit', e)
  }

  const data = {
    id: '2011201908482221234',
    organization: 'My Organization',
    partner: {
      firstName: 'Sabrina',
      middleName: 'Yoshida',
      lastName: 'Rodriques',
      phone: '+` 419 486 2104',
      ext: '056',
      email: 'SabrinaLRodriques@dayrep.com',
      oralEnglish: false,
      oralFrench: true,
      writtenEnglish: false,
      writtenFrench: true
    },
    alternative: {
      firstName: 'Chris',
      middleName: 'Castro',
      lastName: 'Holley',
      phone: '+1 903 372 9902',
      ext: '000',
      email: 'ChrisEHolley@rhyta.com',
      oralEnglish: true,
      oralFrench: false,
      writtenEnglish: true,
      writtenFrench: false
    }
  }

  const handleFillClick = () => {
    if (!state.loading) {
      setState({ ...state, loading: true, generated: false })
      appContext.postFillForm(data, res => {
        console.log(res)
        const filename = JSON.parse(JSON.parse(res.response).body).filename
        console.log('download', filename)
        setState({ ...state, loading: false, generated: true })
      })
    }
  }

  return (
    <Layout title='PDF Forms Filler'>
      <div className={classes.root}>
        <div className={classes.wrapper}>
          <Button color='primary' variant='outlined' disabled>
            Save
          </Button>
        </div>
        <div className={classes.wrapper}>
          <Button
            className={classes.dlButton}
            color='secondary'
            variant='contained'
            endIcon={<GetAppIcon />}
            disabled={!state.generated}>
            Download
          </Button>
        </div>
        <div className={classes.wrapper}>
          <Button
            variant='contained'
            color='primary'
            disabled={state.loading}
            onClick={() => handleFillClick()}>
            FILL TO FORM
          </Button>
          {state.loading && (
            <CircularProgress size={68} className={classes.buttonProgress} />
          )}
        </div>
      </div>
    </Layout>
  )
}

export default Auth
